@use "../common/mixins" as *;
@use "../common/variables" as *;

.page-header {
  display: block;
  border-bottom: 1px solid var(--color-brdr-header);
}

.page-header__logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 13px 0 14px;
}

@media (min-width: $width-tablet) {
  .page-header {
    display: flex;
    align-items: center;
  }

  .page-header__logo {
    gap: 13px;
    justify-content: start;
    margin-right: auto;
  }
}
